import './content.css';
// 账户明细区组件
var AccountDetailsComponent = /** @class */ (function () {
    function AccountDetailsComponent() {
        this.element = document.createElement('div');
        this.element.className = 'linkedin-booster-account-details';
    }
    AccountDetailsComponent.prototype.render = function () {
        this.element.innerHTML = "\n      <div class=\"linkedin-booster-section\">\n        <h3>\u8D26\u6237\u660E\u7EC6\u533A</h3>\n        <div class=\"linkedin-booster-placeholder\">\n          <p>\u663E\u793A\u8D26\u6237\u4FE1\u606F\u3001\u4F7F\u7528\u7EDF\u8BA1\u548C\u8BA2\u9605\u72B6\u6001</p>\n        </div>\n      </div>\n    ";
        return this.element;
    };
    AccountDetailsComponent.prototype.destroy = function () {
        this.element.remove();
    };
    return AccountDetailsComponent;
}());
// LinkedIn 操作区组件
var LinkedInActionsComponent = /** @class */ (function () {
    function LinkedInActionsComponent() {
        this.element = document.createElement('div');
        this.element.className = 'linkedin-booster-linkedin-actions';
        this.isLinkedInPage = window.location.hostname.includes('linkedin.com');
    }
    LinkedInActionsComponent.prototype.render = function () {
        var _this = this;
        this.element.innerHTML = "\n      <div class=\"linkedin-booster-section\">\n        <h3>LinkedIn \u64CD\u4F5C\u533A</h3>\n        ".concat(this.isLinkedInPage ? "\n          <div class=\"linkedin-booster-linkedin-content\">\n            <div class=\"linkedin-booster-search-status\">\n              <p>\u5F53\u524D\u9875\u9762: ".concat(this.getCurrentPageType(), "</p>\n            </div>\n            <div class=\"linkedin-booster-actions\">\n              <button class=\"linkedin-booster-button\" id=\"extract-candidates\">\n                \u63D0\u53D6\u5019\u9009\u4EBA\u4FE1\u606F\n              </button>\n              <button class=\"linkedin-booster-button\" id=\"batch-connect\">\n                \u6279\u91CF\u53D1\u9001\u8FDE\u63A5\u8BF7\u6C42\n              </button>\n            </div>\n          </div>\n        ") : "\n          <div class=\"linkedin-booster-placeholder\">\n            <p>\u8BF7\u5728 LinkedIn \u9875\u9762\u4F7F\u7528\u6B64\u529F\u80FD</p>\n          </div>\n        ", "\n      </div>\n    ");
        // 添加事件监听器
        if (this.isLinkedInPage) {
            var extractButton = this.element.querySelector('#extract-candidates');
            var batchConnectButton = this.element.querySelector('#batch-connect');
            if (extractButton) {
                extractButton.addEventListener('click', function () { return _this.handleExtractCandidates(); });
            }
            if (batchConnectButton) {
                batchConnectButton.addEventListener('click', function () { return _this.handleBatchConnect(); });
            }
        }
        return this.element;
    };
    LinkedInActionsComponent.prototype.getCurrentPageType = function () {
        if (window.location.pathname.includes('/search/')) {
            return '搜索结果页';
        }
        else if (window.location.pathname.includes('/in/')) {
            return '个人资料页';
        }
        else {
            return '其他页面';
        }
    };
    LinkedInActionsComponent.prototype.handleExtractCandidates = function () {
        console.log('提取候选人信息');
    };
    LinkedInActionsComponent.prototype.handleBatchConnect = function () {
        console.log('批量发送连接请求');
    };
    LinkedInActionsComponent.prototype.destroy = function () {
        this.element.remove();
    };
    return LinkedInActionsComponent;
}());
