// 侧边栏类
var Sidebar = /** @class */ (function () {
    function Sidebar() {
        this.isVisible = false;
        console.log('初始化侧边栏');
        this.components = [
            new AccountDetailsComponent(),
            new LinkedInActionsComponent(),
            new AIIntegrationComponent()
        ];
        this.sidebar = this.createSidebar();
    }
    Sidebar.prototype.createSidebar = function () {
        var _this = this;
        console.log('创建侧边栏元素');
        var sidebar = document.createElement('div');
        sidebar.className = 'linkedin-booster-sidebar';
        sidebar.style.transform = 'translateX(100%)'; // 初始状态隐藏
        // 创建头部
        var header = document.createElement('div');
        header.className = 'linkedin-booster-header';
        header.innerHTML = "\n      <h2 class=\"linkedin-booster-title\">LinkedIn Booster</h2>\n      <button class=\"linkedin-booster-close\">&times;</button>\n    ";
        // 创建内容区域
        var content = document.createElement('div');
        content.className = 'linkedin-booster-content';
        // 渲染所有组件
        this.components.forEach(function (component) {
            content.appendChild(component.render());
        });
        // 添加关闭按钮事件监听
        var closeButton = header.querySelector('.linkedin-booster-close');
        if (closeButton) {
            closeButton.addEventListener('click', function () { return _this.hide(); });
        }
        sidebar.appendChild(header);
        sidebar.appendChild(content);
        return sidebar;
    };
    Sidebar.prototype.show = function () {
        var _this = this;
        console.log('显示侧边栏');
        if (!this.isVisible) {
            document.body.appendChild(this.sidebar);
            // 使用 setTimeout 确保过渡动画生效
            setTimeout(function () {
                _this.sidebar.style.transform = 'translateX(0)';
            }, 10);
            this.isVisible = true;
        }
    };
    Sidebar.prototype.hide = function () {
        var _this = this;
        console.log('隐藏侧边栏');
        if (this.isVisible) {
            this.sidebar.style.transform = 'translateX(100%)';
            // 等待过渡动画完成后移除元素
            setTimeout(function () {
                _this.sidebar.remove();
                _this.isVisible = false;
            }, 300);
        }
    };
    Sidebar.prototype.destroy = function () {
        this.components.forEach(function (component) { return component.destroy(); });
        this.sidebar.remove();
        this.isVisible = false;
    };
    return Sidebar;
}());
// 创建侧边栏实例
console.log('开始创建侧边栏');
var sidebar = new Sidebar();
// 监听来自background的消息
chrome.runtime.onMessage.addListener(function (request, sender, sendResponse) {
    console.log('收到消息:', request);
    if (request.action === "toggleSidebar") {
        if (sidebar.isVisible) {
            sidebar.hide();
        }
        else {
            sidebar.show();
        }
    }
    else if (request.action === 'showCloseMessage') {
        // 显示提示消息
        var message_1 = document.createElement('div');
        message_1.className = 'linkedin-booster-message';
        message_1.textContent = '点击扩展图标关闭侧边栏';
        document.body.appendChild(message_1);
        // 3秒后自动移除消息
        setTimeout(function () {
            message_1.remove();
        }, 3000);
    }
});
